@import '../utils/mixins.scss';

.notification {
  display: flex;
  border-radius: 7px;
  font-weight: 700;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 10%;
  right: 3%;
  color: white;

  width: 150px;
  height: 50px;

  background-color: #ff5252;

  animation-name: blink;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: alternate;

  @include onDesktop {
    top: 43%;
    right: 1%;

    width: 300px;
    height: 100px;
  }
}

@keyframes blink {
  from {
    transform: scale(90%);
  }

  to {
    transform: scale(110%);
  }
}
