@import '../../utils/mixins.scss';
@import '../../utils/colors.scss';

.MainPage {
  background-image: radial-gradient(50% 50% at 80% 50%, $pink-gradient 0%, $black-gradient 100%);
  position: relative;
  min-height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  padding: 30px 0 5px;

  @include onDesktop {
    padding: 50px 0 0;
    background-image: linear-gradient(to left bottom, $pink-gradient, transparent 30%, $pink-gradient);
  }

  &__logo {
    margin-bottom: 15%;

    @include onDesktop {
      margin-bottom: 5%;
      width: 224px;
      height: 44px;
    }

    @include on1920 {
      width: 320px;
      height: 63px;
    }
  }

  &__title {
    margin: 0 0 5%;
    font-weight: 700;
    font-size: 26px;
    line-height: 110%;

    @include onDesktop {
      margin: 0 0 3%;
      font-size: 41px;
      line-height: 100%;
    }

    @include on1440 {
      font-size: 62px;
    }

    @include on1920 {
      font-size: 82px;
    }
  }

  &__paragraph {
    font-weight: 400;
    font-size: 14px;
    margin: 0 auto 25%;
    line-height: 150%;

    &--yellow {
      font-weight: 700;
      position: relative;

      border-bottom: 2px solid $yellow-underline;
      transition: 0.3s all;
      
      &:hover {
        background-color: $yellow-underline;
        border-radius: 3px;
        cursor: pointer;
      }
    }

    &--blue {
      font-weight: 700;
      position: relative;

      border-bottom: 2px solid $blue-underline;
      transition: 0.3s all;
      
      &:hover {
        background-color: $blue-underline;
        color: #fff;
        border-radius: 3px;
        cursor: pointer;
      }
    }

    &--red {
      font-weight: 700;
      position: relative;
      transition: 0.3s all;

      border-bottom: 2px solid $red-underline;
      
      &:hover {
        background-color: $red-underline;
        border-radius: 3px;
        cursor: pointer;
      }
    }

    @include onTablet {
      margin: 0 auto 10%;
    }

    @include onDesktop {
      font-size: 11px;
      width: 860px;
      margin: 0 auto 7%;
    }

    @include on1440 {
      font-size: 15px;
    }

    @include on1920 {
      font-size: 20px;
    }
  }

  &__button {
    margin: 0 auto 15px;
    display: block;

    font-weight: 700;
    font-size: 16px;
    line-height: 170.9%;
    text-align: center;

    color: $main-red;
    background-color: transparent;
    border: 2px solid $main-red;
    border-radius: 6px;
    padding: 19px 28px;
    transition: 0.3s all;

    @include onDesktop {
      font-size: 14px;
      line-height: 170.9%;
      padding: 10px 30px;
      border-radius: 10px;
      margin: 0 auto;
    }

    @include on1440 {
      font-size: 21px;
      padding: 15px 45px;
    }

    @include on1920 {
      font-size: 28px;
      padding: 20px 60px;
    }

    &:hover {
      color: $white;
      cursor: pointer;
      background-color: $main-red;
    }
  }

  &__illustration {
    z-index: -1;
    position: absolute;
    left: -6%;
    bottom: 30px;

    animation-name: move;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-iteration-count: 6;
    animation-direction: alternate;

    @include onTablet {
      animation-name: move;
      width: 263px;
      left: -3%;
      height: 197px;
    }

    @include onDesktop {
      animation-name: moveOnDesktop;
      width: 394px;
      height: 295px;
    }

    @include on1440 {
      width: 525px;
      height: 391px;
    }

    @include on1920 {
      left: -5%;
      width: 788.02px;
      height: 591.02px;
    }
  }

  &__social {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 21px;
    font-weight: 400;
    font-size: 10px;
    line-height: 160%;
  }

  &__social-link {
    color: $social-links;
    display: flex;
    gap: 4px;
    align-items: center;
    transition: 0.3s all;

    &:hover {
      transform: scale(115%);
    }
  }
}

@keyframes move {
  from {
    bottom: 30px;
  }

  to {
    bottom: 60px;
  }
}

@keyframes moveOnDesktop {
  from {
    z-index: -1;
    bottom: 30px;
  }

  to {
    z-index: 1;
    bottom: 100px;
  }
}
